import React from 'react';
import { FaRocket, FaBook } from 'react-icons/fa';
import scrollTo from 'gatsby-plugin-smoothscroll';

import heroImage from '../images/hero.svg';

interface Props {
  content: {
    title: string;
    description: string;
    whitepaperButton: string;
    waitlistButton: string;
  };
}

const Hero = ({ content }: Props) => (
  <div className="min-h-screen">
    <div className="flex flex-col md:flex-row-reverse md:items-center">
      <div className="h-96 self-end md:hero-image">
        <img
          src={heroImage}
          className="h-full max-w-none -m-8"
          alt="Exorde superheroes"
          loading="lazy"
        />
      </div>
      <div className="mx-auto -mt-8 md:mt-0 text-center p-4 md:p-16 md:-mr-16">
        <h1 className="lg:text-6xl">{content.title}</h1>
        <p>{content.description}</p>
        <div className="lg:flex mt-6 mx-auto justify-center">
          <button
            onClick={() => scrollTo('#whitepaper')}
            className="bg-blue text-white mx-auto my-2 lg:mx-2 text-sm p-4 rounded-full max-w-max flex items-center transition duration-500 ease-in-out transform hover:-translate-y-1 hover:scale-110"
          >
            {content.whitepaperButton}
            <FaBook className="ml-2" />
          </button>
          <button
            onClick={() => scrollTo('#newsletter')}
            className="bg-orange text-white mx-auto my-2 lg:mx-2 text-sm p-4 rounded-full max-w-max flex items-center transition duration-500 ease-in-out transform hover:-translate-y-1 hover:scale-110"
          >
            {content.waitlistButton}
            <FaRocket className="ml-2" />
          </button>
        </div>
      </div>
    </div>
  </div>
);

export default Hero;
