import React, { useState } from 'react';
import { FaCheck } from 'react-icons/fa';
import { AiOutlineLoading3Quarters } from 'react-icons/ai';

import getNewsletterURI from '../utils/getNewsletterUri';
interface Props {
  title: string;
  description: string;
  form: {
    firstNameLabel: string;
    emailLabel: string;
    buttonLabel: string;
    successLabel: string;
    successMessage: string;
  };
  locale: string;
}

const Newsletter = ({ title, description, form, locale }: Props) => {
  const [email, setEmail] = useState('');
  const [firstName, setFirstName] = useState('');
  const [honeypotName, setHoneypotName] = useState(undefined);
  const [honeypotEmail, setHoneypotEmail] = useState(undefined);
  const [success, setSuccess] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(undefined);
  const handleSubmit = async evt => {
    try {
      if (honeypotName || honeypotEmail) return;
      const newsletterURI = getNewsletterURI();
      setLoading(true);
      evt.preventDefault();
      setError(undefined);
      const { status } = await fetch(`${newsletterURI}/${locale}/contact`, {
        method: 'POST',
        headers: { Accept: 'application/json', 'content-type': 'application/json' },
        body: JSON.stringify({ email, firstName }),
      });
      setSuccess(status >= 200 && status < 300);
      setLoading(false);
    } catch (error) {
      setLoading(false);
      setError(error.message);
    }
  };
  return (
    <div className="bg-grey-light text-blue p-8 py-20 md:p-20" id="newsletter">
      <h2 className="text-center  font-brand pb-4 md:pb-12">{title}</h2>
      <p className="max-w-xl p-2 text-justify mx-auto">{description}</p>
      <form
        className="flex flex-col max-w-xl mx-auto mt-2"
        id="newsletter_form"
        method="POST"
        onSubmit={handleSubmit}
      >
        <input
          type="text"
          name="firstName"
          id="firstName"
          placeholder={`${form.firstNameLabel}*`}
          required
          className="p-4 m-2 rounded focus:ring-2 focus:ring-blue"
          onChange={event => setFirstName(event.target.value)}
        />
        <input
          type="email"
          name="email"
          id="email"
          placeholder={`${form.emailLabel}*`}
          required
          className="p-4 m-2 rounded focus:ring-2 focus:ring-blue"
          onChange={event => setEmail(event.target.value)}
        />
        <input
          className="invisible"
          autoComplete="off"
          type="text"
          id="name2"
          name="name2"
          placeholder="Your name here"
          onChange={event => setHoneypotName(event.target.value)}
        />
        <input
          className="invisible"
          autoComplete="off"
          type="email2"
          id="email2"
          name="email2"
          placeholder="Your e-mail here"
          onChange={event => setHoneypotEmail(event.target.value)}
        />
      </form>
      {error && <p className="text-red max-w-lg text-center mx-auto mb-2">{error}</p>}
      {success && (
        <p className="text-green max-w-lg text-center mx-auto mb-2">{form.successMessage}</p>
      )}
      <button
        disabled={!(firstName && email) || !!honeypotName || !!honeypotEmail || success}
        type="submit"
        form="newsletter_form"
        value="Submit"
        className={`bg-blue text-white p-4 rounded-full max-w-max flex items-center mx-auto transition duration-500 ease-in-out transform hover:-translate-y-1 hover:scale-110 disabled:bg-blue-light disabled:cursor-not-allowed disabled:transform-none ${
          success && 'disabled:bg-green'
        } ${error && 'bg-red'}`}
      >
        {success ? (
          <>
            {form.successLabel}
            <FaCheck className="ml-2" />
          </>
        ) : loading ? (
          <AiOutlineLoading3Quarters className="animate-spin" />
        ) : (
          form.buttonLabel
        )}
      </button>
    </div>
  );
};

export default Newsletter;
