import React, { useState } from 'react';
import { FaPlayCircle } from 'react-icons/fa';

import Video from './video';

interface Props {
  content: {
    title: string;
    description: string;
    url: string;
    thumbnail: string;
  };
}

const Thumbnail = ({ thumbnail, title, handleClick }) => (
  <div
    className="flex items-center justify-center cursor-pointer transition duration-500 ease-in-out transform hover:-translate-y-1 hover:scale-110"
    onClick={() => handleClick()}
  >
    <img className="rounded low-brightness" src={thumbnail} alt={title} loading="lazy" />
    <FaPlayCircle className="text-white text-7xl absolute" />
  </div>
);

const Enemy = ({ content }: Props) => {
  const [displayVideo, setDisplayVideo] = useState(false);
  return (
    <div className="bg-blue text-white p-8 py-20 md:p-20">
      <h2 className="text-center font-brand pb-4 md:pb-12">{content.title}</h2>
      <div className="block md:flex items-center">
        <div className="flex-1">
          {displayVideo ? (
            <Video title={content.title} url={content.url} />
          ) : (
            <Thumbnail
              thumbnail={content.thumbnail}
              title={content.title}
              handleClick={() => setDisplayVideo(true)}
            />
          )}
        </div>
        <p className="flex-1 pt-8 text-justify md:p-8">{content.description}</p>
      </div>
    </div>
  );
};

export default Enemy;
