import React from 'react';

interface Props {
  title: string;
  url: string;
}

const Video = ({ title, url }) => (
  <iframe
    title={title}
    width="100%"
    height="315"
    src={url}
    frameBorder="0"
    allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
    allowFullScreen
    loading="lazy"
  />
);

export default Video;
